.form-control--radio {
  justify-content: space-around;

  .form {
    &__label {
      display: inline-block;
      border: 1px solid #000;
      padding: 5px;
      border-radius: 10px;
    }

    &__radio {
      display: none !important;

      &:checked + label {
        background-color: #c6a1cf;
        border-color: #c6a1cf;
        color: #fff;
      }
    }
  }
}
