.loader-container {
  position: relative;
  border: 1px solid #fff;
  height: 30%;
  top: 35px;
  width: 60%;
  border-radius: 20px;
  overflow: hidden;
  background-color: #c6a1cf;

  & > div {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: #fff;
    right: 69%;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }
}

.percentage {
  position: relative;
  top: 33px;
  font-size: 31pt;
  color: #fff;
  margin-left: 10px;
  font-weight: bold;
}
