.survey-card-container {
  border: 3px solid #8c4799;
  border-radius: 15px;
  margin: 20px 0;
  padding: 0 10px;
  background-color: #c6a1cf;

  & > * {
    color: #6d2077;
  }

  & h3 {
    margin: 5px;
  }

  & span {
    font-style: italic;
    position: relative;
    display: inline-block;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      width: 110%;
      border: 1px solid #6d2077;
      display: block;
    }
  }

  & p {
    margin: 10px 0;
  }
}
