.form-group {
  display: flex;
  flex-direction: column;
  margin: 15px 0;

  & .form-control {
    display: flex;
    margin-top: 5px;

    & > input,
    & > select {
      padding: 10px;
      border: 1px solid #63666a;
      border-radius: 10px;
      display: block;
      width: 100%;
      background-color: #d9d9d6;
    }
  }
}
