.login-container {
  height: 100vh;
  color: #fff;
  padding: 25px;

  &__img {
    display: flex;
    justify-content: center;
  }

  &__img > img {
    height: 55px;
    margin: 25px 0 10px;
  }

  & > h1 {
    font-size: 30pt;
    margin-inline: 40px;
  }

  & > p {
    margin-bottom: 75px;
    font-size: 15pt;
  }
}

.field {
  display: flex;
  justify-content: center;
  margin: 15px 10px;
  height: 40px;

  & > input {
    background-color: #6d2077;
    border: 1px solid #c6a1cf;
    color: #fff;
    padding: 10px;
    font-size: 8pt;
    border-radius: 10px;
    width: 100%;
  }
}
