.profile-header {
  display: flex;
  flex-direction: column;
  align-items: center;

  & > p {
    font-size: 18pt;
    color: #fff;
    font-weight: bold;
    margin: 0;
  }

  & > p:first-of-type {
    margin-top: 25px;
  }

  & > div {
    display: inherit;
    justify-content: center;
    align-items: center;
    position: relative;
    border: 1px solid #c6a1cf;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    margin-top: 10px;
    background: #c6a1cf;

    &::after {
      content: '';
      position: absolute;
      border: 3px solid #babc16;
      border-left: none;
      border-bottom: none;
      border-radius: 50%;
      width: 95px;
      height: 95px;
      transform: rotate3d(0, 0, 1, 75deg);
    }
  }

  &__img {
    width: 50px;
  }
}
