.button {
  border: 1px solid #fff;
  padding: 10px 20px;
  border-radius: 10px;
  font-weight: bold;

  &--default {
    color: #fff;
    background-color: transparent;
  }

  &--inverse {
    color: #8c4799;
    background-color: #fff;
  }

  &--block {
    display: block;
    width: 100%;
  }

  &--fix-bottom {
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 15px 35px;
    border-radius: 30px 0 0 0;
    font-size: 8pt;
  }
}
